import React, { useEffect, useRef, useState } from "react";
import GoogleMapReact from "google-map-react";
import { MapContainer } from "../styles/styles";
import RaptorMarker from "../markers/RaptorMarker";
import whiteMap from "../styles/whiteMap.json";
import Smiler from "../graphics/Smiler";

const CMap = ({ history, mapKey, places }) => {
  const [userLocation, setUserLocation] = useState();
  const [marker, setMarker] = useState([]);
  const mapCRef = useRef();
  const mapRef = useRef();
  useEffect(() => {
    navigator.geolocation.getCurrentPosition(p => {
      const { latitude: lat, longitude: lng } = p.coords;
      setUserLocation({ lat, lng });
    });
    setTimeout(() => {
      const mappo = mapCRef.current;
      mapRef.current = mappo.getElementsByTagName("div")[0];
      mappo.addEventListener("touchstart", () => {
        console.log("touchy");
        console.log(mapRef.current);
        mapRef.current.click();
      });
    }, 1000);
  }, []);

  return (
    <MapContainer ref={mapCRef}>
      {!userLocation ? (
        <Smiler id="smiler" style={{ fill: "red" }} />
      ) : (
        <GoogleMapReact
          // defaultCenter={places.ppark}
          defaultCenter={userLocation}
          defaultZoom={17}
          bootstrapURLKeys={{ key: mapKey }}
          options={{ styles: whiteMap }}
          onClick={e => {
            const marker = {
              key: "frog",
              id: "frog",
              found: false,
              lat: e.lat,
              lng: e.lng
            };
            setMarker(marker);
          }}
          onChildClick={(e, p) => {
            console.log(e);
            return;
            history.push(`/pop/${p}`);
          }}
        >
          <RaptorMarker
            key={marker.hash}
            id={marker.hash}
            found={marker.found}
            lat={marker.lat}
            lng={marker.lng}
          />
        </GoogleMapReact>
      )}
    </MapContainer>
  );
};

export default CMap;
